@import '../mixins.scss';
@import url('https://fonts.googleapis.com/css2?family=Merriweather:wght@400;700&display=swap');


@font-face {
    font-family: 'SF Pro Display';
    src: url('./font/FontsFree-Net-SFProDisplay-Regular.ttf');
  }

  .article-body {
        font-family: 'SF Pro Display';
        text-align: center;
        
        

        .back {
            position: absolute;
            left: 6%;
            top: 34px;

     @include bp(desktop) {
         left: 4%;
        top: 28px;
        }
        @include bp(tablet) {
            left: 11%;
            top: 18px;
        }

        @include bp(mobile) {
            display: none;
        }

    
            &:hover {
                cursor: pointer;
                transform: translateX(-3px);
                transition: all 0.2s ease;
            }
        }

        .close {
            display: none;

            @include bp(mobile) {
                display: flex;
                position: absolute;
                right: 5%;
                top: 4%;
                cursor: pointer;

            }
        }
        
        .close-light {
            
            @include bp(mobile) {
                background: #fff;
            }
        }

        .close-dark {

            @include bp(mobile) {
                background: #111216;
            }
        }

    .header {
        display: flex;
        flex-direction: column;

        @include bp(mobile) {
            margin: 30% 5%;
            text-align: center;
        }

        h1 {
        font-family: 'Merriweather', serif;
        width: 50%;
        align-self: center;
        text-align: left;
       
        @include bp(tablet) {
            width: 70%;
        }   
        @include bp(mobile) {
            width: 100%;
            font-size: 42px;
            margin: 0;
        }

        }

        .image-div-1 {
            display: flex;
            width: 50%;
            align-self: center;
            margin: 50px 0;
           
            @include bp(tablet) {
                width: 70%;
            }

            @include bp(mobile) {
            width: 100%;
            margin: 0;
            }


            img {
                width: 25px;
                height: 25px;
                border-radius: 50%;
                
                @include bp(mobile) {
                    display: none;
                }
                
            }

            .content {
                display: flex;
                color: #73737D;
                margin-left: 5px;
                align-items: center;
                overflow: hidden;
                

                @include bp(mobile) {
                    margin: 5% 0;  
                    flex-direction: column;
                }

                .content-flex {
                    display: flex;

                    .june {
                        margin-left: 5px;
                    }
                }

                
                .name {
                    @include bp(mobile) {
                        width: 100%;
                        text-align: left;
                        margin-bottom:7px;
                    }
                 }


                .info {
                 margin-left: 10px;
                 font-size: 14px;

                 @include bp(mobile) {
                     margin-left: 7px;
                 }


                 span {
                   font-weight: 600;  
                   
                 }
                 
                }

            }
        }
    }

    .nav-right {
        display: none;
  
        @include bp(mobile) {  
        position: absolute;
         display: flex;
         justify-content: flex-end;
         width: 50%;

        }
  
        .logo {
          color: #7A8085;
          margin-left: 22px;
  
          &:hover {
            color: #000;
        }
        }

        .link {
            margin-right: 20px;
          }
    
        .mode {
            position: absolute;
            transition: all 0.3s ease;
          }
    
          .active {
            opacity: 0;
          }

      }

    .image-div-2 {

        width: 100%;
        height: 12%;
        margin: 10% 0 5% 0;
        overflow: hidden;

        @include bp(tablet) {
            width: 100vw;
            transform: translateX(-14%);  
        }

        @include bp(mobile) {
            width: 100%;
            margin: 5%;
            margin-bottom: 10%;
            transform: translateX(-5%);
        }
        
       
        img {
            width: 70%;
            height: 12%;
            box-shadow: (0px 18.6404px 37.2807px rgba(0, 0, 0, 0.15));
        
            @include bp(tablet) {
                width: 100%;
                
            }

            @include bp(mobile) {
                width: 100%;
            }

        
        }
    }

    .paragraphs {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        font-size: 12px;

        @include bp(mobile) {
            margin-top: 20%;
            
        }

        @include bp(desktop) {
            font-size: 15px;   
        }

        @include bp(tablet) {
            font-size: 15px;
        }

        a {
            text-decoration: underline;
            cursor: pointer;
            color: blue;
        }
        

p  {
        width: 40%;
        margin: 15px 0;
        text-align:left;
        line-height: 2;

        @include bp(desktop) {
            width: 50%;
        }

        @include bp(tablet) {
            width: 65%;
        }

        @include bp(mobile) {
            width: 100%;
            font-size: 17px;
        }
    }

     .final {
            font-size: 10px;
            line-height: 1.5;
            width: 40%;
            text-align: left;
            font-weight: 600;

            @include bp(desktop) {
            width: 50%;
            }

            @include bp(tablet) {
                width: 65%;
                font-size: 14px;
            }

            @include bp(mobile) {
                width: 100%;
            }
        
        }

    h2 {
        font-family: 'Merriweather', serif;
        width: 50%;
        font-size: 20px;
        line-height: 1.5;
        margin: 10px 0;
        text-align: left;

        
        @include bp(desktop) {
            width: 60%;   
        }

        @include bp(tablet) {
            width: 55%;
        }

        @include bp(mobile) {
            width: 90%;
            font-size: 45px;
        }
    }
    }

    .more-articles {
        // display: flex;
        margin: 5% 0;
        width: 100%;
        
        @include bp(tablet) {
            margin-top: 15%;
        }

        .left {
            width: 100%;
            position: relative;
            color:#73737D;
            text-align: left;
            font-size: 12px;

            @include bp(mobile) {
              font-size: 18px;  
            }
            

            &:after {
                position: absolute;
                align-self: center;
                content: "";
                width: 80%;
                height: 1px;
                background: #929296;
                top: 6px;
                left: 20%;

                @include bp(tablet) {
                    width: 70%;
                    left: 30%;
                }

                @include bp(mobile) {
                    width: 35%;
                    left: 65%;
                    top: 10px;
                }
            }
        }



    }

    .sections {
        text-align: left;
    }
    
  }