@import url('https://fonts.googleapis.com/css2?family=Merriweather:wght@400;700&display=swap');
@import '../mixins.scss';

@font-face {
    font-family: 'SF Pro Display';
    src: url('./font/FontsFree-Net-SFProDisplay-Regular.ttf');
  }

  .article-page {
    font-family: 'SF Pro Display';
    display: flex;
    flex-direction: column;
    text-align: center;

    

    .back {
        position: absolute;
        left: 7%;
        top: 13%;

        @include bp(desktop) {
            left: 4%;
             top: 11%;
        }

        @include bp(tablet) {
            left: 12%;
             top: 7%;
        }

        @include bp(mobile) {
            left: 2%;
            top: 4%;
        }

        &:hover {
            cursor: pointer;
            transform: translateX(-3px);
            transition: all 0.2s ease;
        }
    }

    .top {
        display: flex;
        flex-direction: column;
        align-items: center;

        .header-image {
            width: 150px;
            height: 150px;
            border-radius: 50%;
            box-shadow: (0px 15.619px 31.2381px rgba(0, 0, 0, 0.15));
            
            

            img {
                    width: 100%;
                    height: 100%;
                    border-radius: 50%;
            }
        }

        .header-title {
            margin: 30px 0 20px 0;

            h1 {
                font-size: 30px;
            }
        }

        .header-b-text {
            color: #73737D;
            align-self: center;
            margin-top: 10px;
            margin-left: 20px; 
            position: relative;
            font-size: 14px;
            width: 33%;
            
            @include bp(tablet) {
                width: 70%;
                font-size: 16px;
            }

            @include bp(mobile) {
                width: 80%;
                font-size: 13px;
                margin-left: 0;
                line-height: 1.6;
            }

            span {
                text-decoration: underline; 
            }
        }


        .socials {
            width: 25%;
            display: flex;
            justify-content: space-evenly;
            color: #73737D;
            margin: 40px 0;

            @include bp(tablet) {
                width: 55%;
            }

            @include bp(mobile) {
                width: 90%;
            }
           
    
            .logo {
    
                &:hover {
                    color:#000;
                }
            }
        }
    }

    .img-bg-light {
         background: linear-gradient(180deg, #FFFFFF 0%, rgba(249, 250, 252, 0) 91.01%);
    }

    .img-bg-dark {
        background: linear-gradient(180deg, #1D2128 0%, rgba(29, 33, 40, 0) 83.4%);
    }

    .image-section {
        border-radius: 5px;
        margin: 40px;

        @include bp(desktop) {
            background: none;
            border-radius: 0;
            margin: 0; 
        }

        @include bp(mobile) {
            margin-bottom: 40px;
        }


        .first {
            display: flex;
            margin: 90px 90px 20px 90px;

            @include bp(desktop) {
