            }

            @include bp(tablet) {
                flex-direction: column;
            }

            @include bp(mobile) {
                margin: 0;
                margin-top: 20px;
            }
        
            :link {
                text-decoration: none;
            }
           
            
                .article-left {
                    width: 50%;
                    margin-right: 15px;

                    @include bp(tablet) {
                        width: 100%;
                    }

                    @include bp(mobile) {
                        border-radius: 6px;
                        box-shadow: (0px 25px 50px rgba(0, 0, 0, 0.15));
                        background: #fff;
                        // margin-top: 10%;
                    }
            
                    .image-div {
                        width: 100%;
                        height: 45vh;
                        box-shadow: (0px 25px 50px rgba(0, 0, 0, 0.15));
                        overflow: hidden;
                        

                        img {
                             width: 100%;
                            height: 100%;
                            background-size: contain;
                            background-position: center;

                            @include bp(mobile) {
                                border-top-left-radius: 6px;
                                border-top-right-radius: 6px;
                            }
                        }
                    }

                    .light {

                        @include bp(mobile) {
                        background: #fff;
                        }
                    }
        
                    .dark {
        
                        @include bp(mobile) {
                        background:#1a1b1f;
                        }
                    }
            
                    .article {
                            margin: 30px 0;
                            font-family: 'SF Pro Display';
                            text-align: left;

                            @include bp(mobile) {
                                padding: 35px 20px;
                                margin: 0;
                                border-bottom-left-radius: 6px;
                                border-bottom-right-radius: 6px;
                            }
        
                            &:hover {
                                cursor: pointer;
                                transform: translate(10px, 10px);
                                transition: all 0.3s ease;
                                }
            
            
                        .article-title {
                            font-family: 'Merriweather', serif;
                            font-size: 25px;
                            font-weight: 700;

                            @include bp(mobile) {
                                color: #fff;
                            }
        
                            &:hover {
                                text-decoration: underline;
                                }
                        }

                        .light-title {
                            color:#000;
                        }
        
                        .dark-title {
                            color: #fff;
                        }
            
                        .article-text {
                            font-size: 16px;
                            color: #828287;
                            width: 80%;
                            margin: 18px 0; 
                            line-height: 1.5;
                        }
            
                        .article-info {
