  .page-list {
    display: flex;
    justify-content: center;
    padding-bottom: 50px;
    color: #828287;
    list-style: none;
    cursor: pointer;

    @include bp(desktop) {
        width: 50%;
        transform: translateX(58%);
        justify-content: center;
        padding-bottom: 0px;
    }

    @include bp(tablet) {
        transform: translateX(48%);
    }

 @include bp(mobile) {
    transform: translateX(53%);
    }


    .list {
        margin: 0 20px;

        @include bp(mobile) {
            margin: 0 15px;
            }
        
    &:hover {
        color: #000;  
    }
    }
    
    .icon {
        margin-top: 5px
    }

    .first {
        margin-left: 0;

        &:hover {
            color: #828287;  
        }
    }
    .page-light {
        color: #000;
    }

    .page-dark {
        color: #fff;
    }

    .last {
        font-weight: 600;

        &:hover {
            color: #828287;  
        }
    }
    

    
}
