@import '../mixins.scss';

@font-face {
    font-family: 'SF Pro Display';
    src: url('./font/FontsFree-Net-SFProDisplay-Regular.ttf');
  }

.footer {
    font-family: 'SF Pro Display';
    display: flex;
    padding: 40px 0;
    color: #73737D;

    @include bp(tablet) {
        flex-direction: column;
    }


    .copyright {
            width: 50%;
            font-size: 14px;
            align-self: center;

            @include bp(tablet) {
                  width: 30%;
                   text-align: center;
                   line-height: 1.4;
                   margin: 10px;
            }

            @include bp(mobile) {
                width: 60%;
            }
            

            span {
                text-decoration: underline;
            }
    }

    .socials {
        width: 50%;
        display: flex;
        justify-content: flex-end;

        @include bp(tablet) {
         margin: 70px 0 40px 0;
         justify-content: center;
         align-self: center;
        }
        
        @include bp(mobile) {
        justify-content: center;
        align-self: center;
        }

        .first {
            &:hover {
                color:#000;
            }
        }

        .logo {
            margin-left: 50px;

            @include bp(mobile) {
                margin-left: 30px;
            } 

            &:hover {
                color:#000;
            }
        }
    }
}