@import '../mixins.scss';
@import url('https://fonts.googleapis.com/css2?family=Merriweather:wght@400;700&display=swap');


@font-face {
    font-family: 'SF Pro Display';
    src: url('./font/FontsFree-Net-SFProDisplay-Regular.ttf');
  }

.section-one {
    display: flex;
    flex-direction: row;

    @include bp(tablet) {
        flex-direction: column;
    }

    @include bp(mobile) {
        margin-top: 30%;
    }


    :link {
        text-decoration: none;
    }
    

    &.one-active {
        flex-direction: column;
    }
   
    
        .article-left {
            width: 60%;
            margin-right: 15px;

            @include bp(desktop) {
                width: 50%;
            }

            @include bp(tablet) {
                width: 100%;
                margin-right: 0;
                margin-top: 20px;
            }
         
          @include bp(mobile) {
             width: 100%;
             border-radius: 6px;
             box-shadow: (0px 25px 50px rgba(0, 0, 0, 0.15));
                    }


            &.left-active {
               display: flex;
             flex-direction: row; 
            }
            
            
            
    
            .image-div {
                width: 100%;
                height: 45vh;
                box-shadow: (0px 25px 50px rgba(0, 0, 0, 0.15));
                overflow: hidden;

                @include bp(tablet) {
                    height: 50vh; 
                }


                
    
                img {
                     width: 100%;
                    height: 100%;
                    background-size: cover;
                    background-position: center;

                       @include bp(mobile) {
                    border-top-left-radius: 6px;
                    border-top-right-radius: 6px;
                }
                    
                }
            }

            .light {

                @include bp(mobile) {
                background: #fff;
                }
            }

            .dark {

                @include bp(mobile) {
                background:#1a1b1f;
                }
            }
    
            .article {
                    margin: 30px 0;
                    font-family: 'SF Pro Display';
                    

                    @include bp(mobile) {
                        padding: 35px 20px;
                        margin: 0;
                        border-bottom-left-radius: 6px;
                        border-bottom-right-radius: 6px;
                    }
                    

                    
                 &:hover {
                     cursor: pointer;
                     transform: translate(10px, 10px);
                     transition: all 0.3s ease;
                     }
    
    
                .article-title {
                    font-family: 'Merriweather', serif;
                    font-size: 25px;
                    font-weight: 700;

                    @include bp(mobile) {
                        color: #fff;
                    }

                   

                    &:hover {
                        text-decoration: underline;
                        }
                }

                .light-title {
                    color:#000;
                }

                .dark-title {
                    color: #fff;
                }
    
                .article-text {
                    font-size: 16px;
                    color: #828287;
                    width: 80%;
                    margin: 18px 0; 
                    line-height: 1.5;

                    @include bp(mobile) {
                        width: 100%;
                    }
                }
    
             .article-info {
                display: flex;
                font-size: 16px;
                
    
    
                .date {
                    margin-right: 5px;
                }
    
                .time {
                    margin-left: 3px;
                }
            }

            .light-info {
                color: #c4c4c5;
            }

            .dark-info {
                color: #444444;
            }

            }
    
            
        }
    
        .article-right {
            width: 40%;
            margin-left: 15px;

            @include bp(desktop) {
                width: 50%;
            }

            @include bp(tablet) {
                width: 100%;
                margin-left: 0;
                margin-top: 30px;
            }

            
          @include bp(mobile) {
            width: 100%;
            border-radius: 6px;
            box-shadow: (0px 25px 50px rgba(0, 0, 0, 0.15));
            margin-top: 10%;

            
            
        }

            &.right-active {
                display: flex;
               flex-direction: row;
            }
            
    
            .image-div {
                width: 100%;
                height: 45vh;
                box-shadow: (0px 25px 50px rgba(0, 0, 0, 0.15));
                overflow: hidden;
                
                @include bp(tablet) {
                    height: 50vh; 
                }
                

                img {
                    width: 100%;
                    height: 100%;
                    background-size: cover;
                   background-position: center;

                    @include bp(mobile) {
                    border-top-left-radius: 6px;
                    border-top-right-radius: 6px;
                }
                }
            }
            

            .light {

                @include bp(mobile) {
                background: #fff;
                }
            }

            .dark {

                @include bp(mobile) {
                background:#1a1b1f;
                }
            }

            .article {
                margin: 30px 0;
                font-family: 'SF Pro Display';

                @include bp(mobile) {
                    padding: 35px 20px;
                    margin: 0;
                    border-bottom-left-radius: 6px;
                    border-bottom-right-radius: 6px;
                }

                &:hover {
                    cursor: pointer;
                    transform: translate(10px, 10px);
                    transition: all 0.3s ease;

                    }
    
    
                .article-title {
                    font-family: 'Merriweather', serif;
                    font-size: 25px;
                    font-weight: 700;
                    width: 80%;
                    


                    &:hover {
                        text-decoration: underline;
                        }
                }

                .light-title {
                    color:#000;
                }

                .dark-title {
                    color: #fff;
                }
    
                .article-text {
                        padding: 25px;

                        @include bp(mobile) {
                            width: 100%;
                        }
                } 
                
            .article-info {
                display: flex;
                font-size: 16px;
    
                .date {
                    margin-right: 5px;
                }
    
                .time {
                    margin-left: 3px;
                }
            }

            .light-info {
                color: #c4c4c5;
            }

            .dark-info {
                color: #444444;
            }
            }
    
        }
    // }

}

