@import '../mixins.scss';
@import '../variables.scss';
@import url('https://fonts.googleapis.com/css2?family=Tinos:wght@400;700&display=swap');




.nav {
    font-family: 'Tinos', serif;
    display: flex;
    padding: 6% 0;

    .nav-left {
      display: flex;
      width: 50%;
      align-items: center;

      .logo {
        color: #08080B;
        align-self: center;
      }

      .dark-logo {  
         color: #fafafa;
   }

    .narative-div {
        font-size: 26px;
        margin-left: 6px;
        

        @include bp(tablet) {
          display: none;
        }
 
     }

     .light-nar {
       color: #08080B;
     }
     
     .dark-nar {   
           color: #fafafa;
     }
     
     
     

      .themes-div {
        color: #b6b5b5;
        font-size: 26px;
        margin-left: 6px;

        
        @include bp(tablet) {
          display: none;
        }
      }
      
    }

    .nav-right {
      display: flex;
      justify-content: flex-end;
      width: 50%;
      align-items: center;

      // @include bp(mobile) {
      //   display: none;
      // }

      .logo {
        color: #7A8085;
        
        &:hover {
          color: #000;
      }
      }

      .link {
        margin-right: 20px;
      }

      .mode {
        position: absolute;
        transition: all 0.3s ease;
      }

      .active {
        opacity: 0;
      }

      



    }
    
}