@import url('https://fonts.googleapis.com/css2?family=Merriweather:wght@400;700&display=swap');
@import '../mixins.scss';
@font-face {
    font-family: 'SF Pro Display';
    src: url('./font/FontsFree-Net-SFProDisplay-Regular.ttf');
  }



.column {
    font-family: 'SF Pro Display';

    :link {
        text-decoration: none;
    }

    .header-text {
        font-size: 40px;
        width: 40%;
        // line-height: 1.5;
        font-weight: bold;

        @include bp(desktop) {
            font-size: 38px; 
            width: 50%;
        }

        @include bp(tablet) {
            font-size: 40px; 
            width: 72%;
            margin: 28px 0;
            letter-spacing: 1px;
            line-height: 1.2;
        }

        @include bp(mobile) {
            padding-left: 20px;
            font-size: 30px;
            margin: 0;
            width: 100%;
        }
    }

    .header-bottom {
        display: flex;
        padding: 8% 0;
        align-items: center;

        @include bp(mobile) {
            
            margin-top: 40px;
            padding: 0;
        }

        .header-left {
            width: 150%;
            display: flex;

            @include bp(tablet) {
                width: 100%;
            }
            

            .header-image {
                width: 55px;
                height: 55px;
                border-radius: 50%;
                display: flex;
                align-content: center;
                justify-content: center;

                @include bp(tablet) {
                    width:15%;
                    height: 70%;
                }

                @include bp(mobile) {
                    display: none;
                }
                
                img {
                        margin: 4px;
                        width: 85%;
                        height: 85%;
                        border-radius: 50%;
                        
                }
            }

            .border-light {
                border:  #c3c3c5 1px solid;
            }

            .border-dark {
                border: #000 1px solid;
            }

            .header-b-text {
                color: #73737D;
                align-self: center;
                margin-left: 20px; 
                position: relative;
                font-size: 15px;
                width: 60%;

                @include bp(tablet) {
                    width: 80%;
                    margin-left: 10px;
                    margin-top: 5px;
                    font-size: 15px;
                    padding-left: 10px;
                }

                &:hover {
                    text-decoration: underline;
                    font-weight: bold;
                    font-style: italic;
                    transition: all 0.3s ease;
                }

                span {
                    text-decoration: underline;
                }
            }
        }

        .header-right {
            width: 50%;
            display: flex;
            justify-content: flex-end;
            align-items: center;

            @include bp(desktop) {
                display: none;
            }
          
            
                .logo {
                    padding: 8px 10px;
                    pointer-events: all;
                }

                .logo-left {
                    border-radius: 50%;
                    margin-left: 15px;

                    &:hover {
                       background: #ececec; 
                    }
                }

                .light-left {
                    color: #000;
 
                     &:hover {
                        background: #ececec; 
                     }
                }
 
                .dark-left {
                     color: #fff;
 
                     &:hover {
                         background: #272727; 
                      }
                 }

                .logo-right {
                    color: #a7a3a3;
                    border-radius: 50%;
                    margin-left: 15px;

                    &:hover {
                       background: #ececec; 
                    }
                }

                .dark-right {
                    color: #a7a3a3;
                    border-radius: 50%;
                    margin-left: 15px;

                    &:hover {
                        background: #272727; 
                     }
                }
                
        
        }
    }

    .page-list {
        display: flex;
        color: #828287;
        list-style: none;
        padding-bottom: 50px;
        cursor: pointer;
        justify-content: left;

        @include bp(desktop) {
            width: 50%;
            transform: translateX(58%);
            justify-content: center;
        }

        @include bp(tablet) {
            transform: translateX(48%);
        }

        @include bp(mobile) {
            transform: translateX(53%);
            }



        .list {
            margin: 0 20px;
            cursor:pointer;

            @include bp(mobile) {
                margin: 0 17px;
                }
            
        &:hover {
            color: #000;  
        }
        }
        
        .icon {
            margin-top: 5px
        }

        .first {
            margin-left: 0;

            &:hover {
                color: #828287;  
            }
        }

        .page-light {
            color: #000;
        }

        .page-dark {
            color: #fff;
        }

        .last {
            font-weight: 600;

            &:hover {
                color: #828287;  
            }
        }
        

        
    }

    .section {
        display: flex;
        margin: 50px 0;

        @include bp(tablet) {
            flex-direction: column;
        }

        :link {
            text-decoration: none;
            width: 50%;
            padding: 0;
        }
        
        .image-div {
            width: 50%;
            height: 45vh;
            box-shadow: (0px 25px 50px rgba(0, 0, 0, 0.15));
            overflow: hidden;

            @include bp(tablet) {
                width: 100%;
                height: 50vh;
            }

            img {
                 width: 100%;
                height: 100%;
                background-size: cover;
                background-position: center;
            }
        }

        .article {
                width: 40%;
                margin: 30px 60px;
                font-family: 'SF Pro Display';

                @include bp(tablet) {
                    width: 100%;
                    margin: 30px 0;
                }

                &:hover {
                    cursor: pointer;
                    transform: translate(10px, 10px);
                    transition: all 0.3s ease;
                    }


            .article-title {
                font-family: 'Merriweather', serif;
                font-size: 25px;
                font-weight: 700;

                &:hover {
                    text-decoration: underline;
                }
            }

            .light-title {
                color:#000;
            }

            .dark-title {
                color: #fff;
            }

            .article-text {
                font-size: 15px;
                color: #828287;
                width: 100%;
                margin: 18px 0; 
                line-height: 1.5;
            }

            .article-info {
            display: flex;
            font-size: 16px;


            .date {
                margin-right: 5px;
            }

            .time {
                margin-left: 3px;
            }
        }

        .light-info {
            color: #c4c4c5;
        }

        .dark-info {
            color: #444444;
        }
        }

        
    }
}