body {
    padding: 0 8%;
    background: $light-primary;
    color: #000;

    @include bp(desktop) {
        padding: 0 6%;  
    }


    @include bp(tablet) {
        padding: 0 14%;
    }

    @include bp(mobile) {
        padding:0 5%;
    }


    hr {
        margin-top: 80px;
        border-color: #7e7e83;
        height: 1px;
        border-top: 1px;

        @include bp(mobile) {
            display: none;
        }
        // @include for-tablet-portrait-up {
