@import '../mixins.scss';
@import '../variables.scss';
@font-face {
    font-family: 'SF Pro Display';
    src: url('./font/FontsFree-Net-SFProDisplay-Regular.ttf');
  }




.header {
    font-family: 'SF Pro Display';
    

    @include bp(mobile) {
        margin-top: 30%;
    }

    

    :link {
        text-decoration: none;
    }
    

   .container {
        
        .header-container {
            overflow: hidden;

               .header-text {
        font-size: 40px;
         margin: 9px 0;
         width: 40%;
        font-weight: bold;
         opacity: 1;

        @include bp(desktop) {
            font-size: 38px; 
        }

        @include bp(tablet) {
            // margin: 28px 0;
            letter-spacing: 1px;
        }

        @include bp(mobile) {
            padding-left: 10px;
            font-size: 25px;
            margin: 0;
            width: 100%;
        }


    }
        }

     
    }
    

    .header-bottom {
        display: flex;
        padding: 8% 0;
        align-items: center;

        @include bp(mobile) {
            
            margin-top: 40px;
            padding: 0;
        }

        .header-left {
            width: 150%;
            display: flex;


            @include bp(tablet) {
                width: 100%;
            }
            
            

            

            

            .header-image {
                display: flex;
                width: 55px;
                height: 55px;
                border-radius: 50%;
                align-content: center;
                justify-content: center;

                @include bp(tablet) {
                    width:15%;
                    height: 70%;
                }
                
             @include bp(mobile) {
            display: none;
        }

                img {
                        margin: 4px;
                        width: 85%;
                        height: 85%;
                        border-radius: 50%;
                        
                }
            }

            .border-light {
                border:  #c3c3c5 1px solid;
            }

            .border-dark {
                border: #000 1px solid;
            }

            .header-b-text {
                color: #73737D;
                align-self: center;
                margin-top: 10px;
                margin-left: 20px; 
                position: relative;
                font-size: 15px;
                width: 60%;

                @include bp(tablet) {
                    width: 80%;
                    margin-left: 10px;
                    margin-top: 5px;
                    font-size: 15px;
                    padding-left: 10px;
                }

                @include bp(mobile) {
                    font-size: 16px;
                    width: 90%;
                    margin-left: 0;
                    line-height: 1.3;
                }

               

                &:hover {
                    text-decoration: underline;
                    font-weight: bold;
                    font-style: italic;
                    transition: all 0.3s ease;
                }


                span {
                   text-decoration: underline;  
                }
            }
        }

        .header-right {
            width: 50%;
            display: flex;
            justify-content: flex-end;
            align-items: center;

            @include bp(desktop) {
                display: none;
            }
          
            
                .logo {
                    padding: 8px 10px;
                    pointer-events: all;
                }

                .logo-left {
                    border-radius: 50%;
                    // margin-left: 15px;

                }

               .light-left {
                   color: #000;

                    &:hover {
                       background: #ececec; 
                    }
               }

               .dark-left {
                    color: #fff;

                    &:hover {
                        background: #272727; 
                     }
                }

                

                .logo-right {
                    color: #a7a3a3;
                    border-radius: 50%;
                    margin-left: 15px;

                    &:hover {
                       background: #ececec; 
                    }
                }

                .dark-right {
                    color: #a7a3a3;
                    border-radius: 50%;
                    margin-left: 15px;

                    &:hover {
                        background: #272727; 
                     }
                }

                
                
        
        }
    }

    .grid {

        @include bp(mobile) {
            margin: auto;
        }
    }

    .page-list {
        display: flex;
        color: #828287;
        list-style: none;
        padding-bottom: 50px;
        cursor: pointer;
        justify-content: left;

        @include bp(desktop) {
            width: 50%;
            transform: translateX(58%);
            justify-content: center;
            padding-bottom: 30px;
        }

        @include bp(tablet) {
            transform: translateX(48%);
        }

     @include bp(mobile) {
        transform: translateX(53%);
        }

        

        .list {
            margin: 0 20px;

            @include bp(mobile) {
                margin: 0 15px;
                }
            
        &:hover {
            color: #000;  
        }
        }
        
        .icon {
            margin-top: 5px
        }

        .first {
            margin-left: 0;

            &:hover {
                color: #828287;  
            }
        }

        .page-light {
            color: #000;
        }

        .page-dark {
            color: #fff;
        }

        .last {
            font-weight: 600;

            &:hover {
                color: #828287;  
            }
        }
        

        
    }
}


  